.header {
    max-width: 100%;
    margin: 0 auto;
    padding-top: 45px;
    padding-bottom: 41px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    }
    
    @media screen and (max-width: 1023px) {
        .header {
            margin: 0; 
        }
    }
     
    @media screen and (max-width: 767px) {
        .header {
            max-width: 320px;
            margin: 0;
            padding: 0;
            padding-top: 28px;
        }
    }